import axios, { AxiosRequestConfig, AxiosResponse } from "axios";
import { User, userType } from "../type";
export interface nu {
  today: number;
  week: number;
  month: number;
  all: number;
}
class UserService {
  static async get(config: AxiosRequestConfig): Promise<User> {
    try {
      let url = `${process.env.REACT_APP_API_URL || ""}user`;
      const response = await axios.get<User>(url, config);
      return response.data;
    } catch (error) {
      // @ts-ignore
      throw error;
    }
  }
  static async changePassword(newPassword: string, oldPassword: string, config: AxiosRequestConfig): Promise<User[]> {
    try {
      let url = `${process.env.REACT_APP_ADMIN_URL || ""}user/change-password`;
      const response = await axios.put<User[]>(url, { newPassword, oldPassword }, config);
      return response.data;
    } catch (error) {
      // @ts-ignore
      throw error;
    }
  }
}

export default UserService;
