export interface User {
  username: string;
  password: string;
  updated_at: string;
  created_at: string;
  accounts?: Accounts[];
  type: userType;
  id: string;
}
export interface ApiUser {
  username: string;
  password: string;
  updated_at: string;
  created_at: string;
  id: string;
}
export interface Accounts {
  code: string;
  id: string;
}
export interface Analytics {
  id: string;
  accountcode: string;
  date: string;
  total_calls: number;
  answered_calls: number;
  missed_calls: number;
  failed_calls: number;
  total_duration: number;
  avg_duration: number;
  busiest_hour: number;
  most_called_number: string;
  most_frequent_caller: string;
}
export enum userType {
  "admin",
  "user",
}
