import React, { useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";
import { Button, Card, Col, Modal, Row, message } from "antd";
import { AxiosError } from "axios";
import { CheckOutlined, DeleteOutlined, EditOutlined, MoneyCollectOutlined } from "@ant-design/icons";
import useColors from "../Hooks/useColors";
import useConfig from "../Hooks/useConfig";
import ReactPaginate from "react-paginate";
import { Accounts, Analytics, User, userType } from "../type";
import AnalyticsAdminService from "../services/AnalyticsAdminService";
import AccountService from "../services/AccountService";
import UserService from "../services/UserService";
const key = "Loading";
const AnalyticsScreen: React.FC = () => {
  const [, Colors] = useColors();
  const navigate = useNavigate();
  const [messageApi, contextHolder] = message.useMessage();
  const [result, setresult] = useState<Analytics>();
  const [month, setmonth] = useState<string>("");
  const [account, setaccount] = useState<string>();
  const [alltime, setalltime] = useState<boolean>(false);
  const [year, setyear] = useState<boolean>(false);
  const [accounts, setaccounts] = useState<Accounts[]>([]);
  const [user, setuser] = useState<User>();
  const config = useConfig();
  const logout = () => {
    localStorage.removeItem("token");
    localStorage.removeItem("user_id");
    navigate("/Login", { replace: true });
  };
  const ErrorMessage = () => {
    messageApi.open({
      key,
      type: "error",
      content: "Error!",
      duration: 2,
    });
  };
  const successMessage = () => {
    messageApi.open({
      key,
      type: "success",
      content: "تم!",
      duration: 2,
    });
  };
  useEffect(() => {
    const fetchData = async () => {
      try {
        messageApi.open({
          key,
          type: "loading",
          content: "جار التحميل...",
          duration: 0,
        });
        const user = await UserService.get(config);
        if (user.type == userType.admin) {
          const result = await AccountService.get(config);
          setaccounts(result);
        } else setaccounts(user.accounts || []);
        setuser(user);
        successMessage();
      } catch (error) {
        // Handle error
        ErrorMessage();

        const err = error as AxiosError<{ error: string }>;
        if (err.response?.status === 401) {
          logout();
        }
      }
    };
    fetchData();
  }, []);
  useEffect(() => {
    const fetchData = async () => {
      try {
        messageApi.open({
          key,
          type: "loading",
          content: "جار التحميل...",
          duration: 0,
        });
        const result = await AnalyticsAdminService.getAllTimeAnalytics(config);
        setresult(result);
        successMessage();
      } catch (error) {
        // Handle error
        ErrorMessage();

        const err = error as AxiosError<{ error: string }>;
        if (err.response?.status === 401) {
          logout();
        }
      }
    };
    fetchData();
  }, []);
  const submit = async () => {
    try {
      messageApi.open({
        key,
        type: "loading",
        content: "جار التحميل...",
        duration: 0,
      });
      var result;
      if (user?.type == userType.admin) {
        if (alltime) {
          if (account && account !== "no") result = await AnalyticsAdminService.getAllTimeAnalyticsByAccount(account, config);
          else result = await AnalyticsAdminService.getAllTimeAnalytics(config);
        } else if (year) {
          if (account && account !== "no") result = await AnalyticsAdminService.getYearlyAnalyticsByAccount(account, month.split("-")[0], config);
          else result = await AnalyticsAdminService.getYearlyAnalytics(month.split("-")[0], config);
        } else {
          if (account && account !== "no") result = await AnalyticsAdminService.getMonthlyAnalyticsByAccount(account, month, config);
          else result = await AnalyticsAdminService.getMonthlyAnalytics(month, config);
        }
      } else if (account) {
        if (alltime) {
          result = await AnalyticsAdminService.getAllTimeAnalyticsByAccount(account, config);
        } else if (year) {
          result = await AnalyticsAdminService.getYearlyAnalyticsByAccount(account, month.split("-")[0], config);
        } else {
          result = await AnalyticsAdminService.getMonthlyAnalyticsByAccount(account, month, config);
        }
      }
      setresult(result);
      successMessage();
    } catch (error) {
      // Handle error
      ErrorMessage();

      const err = error as AxiosError<{ error: string }>;
      if (err.response?.status === 401) {
        logout();
      }
    }
  };
  return (
    <>
      <div className="container p-3 pt-4">
        {contextHolder}
        <div className="justify-content-center d-flex flex-row">
          <div>
            <div className="form-floating mb-3">
              <select
                className="form-control"
                onChange={(e) => {
                  setaccount(e.target.value);
                  console.log(e.target.value);
                }}
              >
                {user?.type == userType.admin && <option value={"no"}>الكل</option>}
                {accounts.map((account) => (
                  <option value={account.code}>{account.code}</option>
                ))}
              </select>
              <label htmlFor="floatingInput">اختر الحساب</label>
            </div>
            <div className="mb-3">
              <input
                type="checkbox"
                value={month}
                onChange={(e) => {
                  setalltime(!alltime);
                }}
              />
              <label htmlFor="floatingInput">كل الوقت</label>
            </div>
            <div className="mb-3">
              <input
                type="checkbox"
                value={month}
                disabled={alltime}
                onChange={(e) => {
                  setyear(!year);
                }}
              />
              <label htmlFor="floatingInput">كل العام</label>
            </div>
            <div className="form-floating mb-3">
              <input
                type="month"
                className="form-control"
                value={month}
                disabled={alltime}
                onChange={(e) => {
                  setmonth(e.target.value);
                  console.log(e.target.value);
                }}
              />
              <label htmlFor="floatingInput">اختر التاريخ</label>
            </div>
            <button onClick={submit} className="btn btn-primary">
              ابحث
            </button>
          </div>
          <Row gutter={24}>
            <Col span={24}>
              <Card title="مكالمة تم الرد عليها" bordered={false}>
                مكالمة{result?.answered_calls}
              </Card>
              <Card title="متوسط وقت المكالمات" bordered={false}>
                {result?.avg_duration}
              </Card>
              <Card title="ساعة الذروة" bordered={false}>
                {result?.busiest_hour}
              </Card>
              <Card title="مكالمات لم تكتمل" bordered={false}>
                مكالمة{result?.failed_calls}
              </Card>
              <Card title="المكالمات الفائته" bordered={false}>
                مكالمة{result?.missed_calls}
              </Card>
              <Card title="الرقم الأكثر اتصال به" bordered={false}>
                {result?.most_called_number}
              </Card>
              <Card title="المتصل الاكثر اتصالا" bordered={false}>
                {result?.most_frequent_caller}
              </Card>
              <Card title="الوقت الكلي" bordered={false}>
                {result?.total_duration}
              </Card>
              <Card title="كل المكالمات" bordered={false}>
                مكالمة{result?.total_calls}
              </Card>
            </Col>
          </Row>
        </div>
      </div>
    </>
  );
};

export default AnalyticsScreen;
