import axios, { AxiosRequestConfig, AxiosResponse } from "axios";
import { Accounts, Analytics, ApiUser, User, userType } from "../type";
export interface nu {
  today: number;
  week: number;
  month: number;
  all: number;
}
class ApiUsersService {
  static async get(config: AxiosRequestConfig): Promise<ApiUser[]> {
    try {
      let url = `${process.env.REACT_APP_ADMIN_URL || ""}api-users`;
      const response = await axios.delete<ApiUser[]>(url, config);
      return response.data;
    } catch (error) {
      // @ts-ignore
      throw error;
    }
  }
  static async delete(id: string, config: AxiosRequestConfig): Promise<void> {
    try {
      let url = `${process.env.REACT_APP_ADMIN_URL || ""}api-users/${id}`;
      const response = await axios.delete<void>(url, config);
      return response.data;
    } catch (error) {
      // @ts-ignore
      throw error;
    }
  }
  static async update(id: string, username: string, password: string, config: AxiosRequestConfig): Promise<void> {
    try {
      let url = `${process.env.REACT_APP_ADMIN_URL || ""}api-users/${id}`;
      const response = await axios.put<void>(url, { username, password }, config);
      return response.data;
    } catch (error) {
      // @ts-ignore
      throw error;
    }
  }
  static async create(username: string, password: string, config: AxiosRequestConfig): Promise<void> {
    try {
      let url = `${process.env.REACT_APP_ADMIN_URL || ""}api-users`;
      const response = await axios.post<void>(url, { username, password }, config);
      return response.data;
    } catch (error) {
      // @ts-ignore
      throw error;
    }
  }
}

export default ApiUsersService;
