import axios, { AxiosRequestConfig, AxiosResponse } from "axios";
import { Analytics, User, userType } from "../type";
export interface nu {
  today: number;
  week: number;
  month: number;
  all: number;
}
class AnalyticsAdminService {
  static async getMonthlyAnalytics(date: string, config: AxiosRequestConfig): Promise<Analytics> {
    try {
      let url = `${process.env.REACT_APP_ADMIN_URL || ""}analytics/getMonthlyAnalytics/${date}`;
      const response = await axios.get<Analytics>(url, config);
      return response.data;
    } catch (error) {
      // @ts-ignore
      throw error;
    }
  }
  static async getYearlyAnalytics(date: string, config: AxiosRequestConfig): Promise<Analytics> {
    try {
      let url = `${process.env.REACT_APP_ADMIN_URL || ""}analytics/getYearlyAnalytics/${date}`;
      const response = await axios.get<Analytics>(url, config);
      return response.data;
    } catch (error) {
      // @ts-ignore
      throw error;
    }
  }
  static async getAllTimeAnalytics(config: AxiosRequestConfig): Promise<Analytics> {
    try {
      let url = `${process.env.REACT_APP_ADMIN_URL || ""}analytics/getAllTimeAnalytics`;
      const response = await axios.get<Analytics>(url, config);
      return response.data;
    } catch (error) {
      // @ts-ignore
      throw error;
    }
  }
  static async getMonthlyAnalyticsByAccount(accountcode: string, date: string, config: AxiosRequestConfig): Promise<Analytics> {
    try {
      let url = `${process.env.REACT_APP_ADMIN_URL || ""}analytics/getMonthlyAnalyticsByAccount/${accountcode}/${date}`;
      const response = await axios.get<Analytics>(url, config);
      return response.data;
    } catch (error) {
      // @ts-ignore
      throw error;
    }
  }
  static async getYearlyAnalyticsByAccount(accountcode: string, date: string, config: AxiosRequestConfig): Promise<Analytics> {
    try {
      let url = `${process.env.REACT_APP_ADMIN_URL || ""}analytics/getYearlyAnalyticsByAccount/${accountcode}/${date}`;
      const response = await axios.get<Analytics>(url, config);
      return response.data;
    } catch (error) {
      // @ts-ignore
      throw error;
    }
  }
  static async getAllTimeAnalyticsByAccount(accountcode: string, config: AxiosRequestConfig): Promise<Analytics> {
    try {
      let url = `${process.env.REACT_APP_ADMIN_URL || ""}analytics/getAllTimeAnalyticsByAccount/${accountcode}`;
      const response = await axios.get<Analytics>(url, config);
      return response.data;
    } catch (error) {
      // @ts-ignore
      throw error;
    }
  }
}

export default AnalyticsAdminService;
