import axios, { AxiosRequestConfig, AxiosResponse } from "axios";
import { Accounts, Analytics, User, userType } from "../type";
export interface nu {
  today: number;
  week: number;
  month: number;
  all: number;
}
class AccountService {
  static async get(config: AxiosRequestConfig): Promise<Accounts[]> {
    try {
      let url = `${process.env.REACT_APP_ADMIN_URL || ""}accounts`;
      const response = await axios.get<Accounts[]>(url, config);
      return response.data;
    } catch (error) {
      // @ts-ignore
      throw error;
    }
  }
  static async delete(id: string, config: AxiosRequestConfig): Promise<void> {
    try {
      let url = `${process.env.REACT_APP_ADMIN_URL || ""}accounts/${id}`;
      const response = await axios.delete<void>(url, config);
      return response.data;
    } catch (error) {
      // @ts-ignore
      throw error;
    }
  }
  static async update(id: string, code: string, config: AxiosRequestConfig): Promise<void> {
    try {
      let url = `${process.env.REACT_APP_ADMIN_URL || ""}accounts/${id}`;
      const response = await axios.put<void>(url, { code }, config);
      return response.data;
    } catch (error) {
      // @ts-ignore
      throw error;
    }
  }
  static async create(code: string, config: AxiosRequestConfig): Promise<void> {
    try {
      let url = `${process.env.REACT_APP_ADMIN_URL || ""}accounts`;
      const response = await axios.post<void>(url, { code }, config);
      return response.data;
    } catch (error) {
      // @ts-ignore
      throw error;
    }
  }
}

export default AccountService;
