import axios, { AxiosRequestConfig, AxiosResponse } from "axios";
import { User, userType } from "../type";
export interface nu {
  today: number;
  week: number;
  month: number;
  all: number;
}
class UserAdminService {
  static async create(username: string, password: string, type: userType, config: AxiosRequestConfig): Promise<number> {
    try {
      const response = await axios.post<number>(`${process.env.REACT_APP_ADMIN_URL || ""}user`, { username, password, type }, config);
      return response.data;
    } catch (error) {
      // @ts-ignore
      throw { message: error.response?.data ?? error.message, status: error.response?.status };
    }
  }

  static async addingAccountToUser(user_id: string, account_id: string, config: AxiosRequestConfig): Promise<void> {
    try {
      await axios.get<void>(`${process.env.REACT_APP_ADMIN_URL || ""}user/accounts/${user_id}/${account_id}`, config);
    } catch (error) {
      // @ts-ignore
      throw error;
    }
  }
  static async removeAccountFromUser(user_id: string, account_id: string, config: AxiosRequestConfig): Promise<void> {
    try {
      await axios.delete<void>(`${process.env.REACT_APP_ADMIN_URL || ""}user/accounts/${user_id}/${account_id}`, config);
    } catch (error) {
      // @ts-ignore
      throw error;
    }
  }
  static async delete(id: string, config: AxiosRequestConfig): Promise<void> {
    try {
      await axios.delete<void>(`${process.env.REACT_APP_ADMIN_URL || ""}user/${id}`, config);
    } catch (error) {
      // @ts-ignore
      throw error;
    }
  }
  static async updateType(id: string, isAdmin: boolean, config: AxiosRequestConfig): Promise<void> {
    try {
      await axios.put<void>(`${process.env.REACT_APP_ADMIN_URL || ""}user/updateType/${id}`, { isAdmin }, config);
    } catch (error) {
      // @ts-ignore
      throw error;
    }
  }
  static async update(id: string, username: string, password: string, config: AxiosRequestConfig): Promise<void> {
    try {
      await axios.put<void>(`${process.env.REACT_APP_ADMIN_URL || ""}user/${id}`, { username, password }, config);
    } catch (error) {
      // @ts-ignore
      throw error;
    }
  }

  static async login(username: string, password: string): Promise<{ token: string; user_id: number }> {
    const response: AxiosResponse<{ token: string; user_id: number }> = await axios.post(`${process.env.REACT_APP_BASE_URL || ""}login`, {
      username,
      password,
    });
    return response.data;
  }

  static async logout(config: any): Promise<void> {
    await axios.get(`${process.env.REACT_APP_ADMIN_URL || ""}user/logout`, config);
  }
  static async get(config: AxiosRequestConfig, page: number, type: userType): Promise<{ users: User[]; pages: number }> {
    try {
      let url = `${process.env.REACT_APP_ADMIN_URL || ""}user`;
      url += "?page=" + page;
      url += "&type=" + type;

      const response = await axios.get<{ users: User[]; pages: number }>(url, config);
      return response.data;
    } catch (error) {
      // @ts-ignore
      throw error;
    }
  }
  static async search(keyword: string, config: AxiosRequestConfig): Promise<User[]> {
    try {
      let url = `${process.env.REACT_APP_ADMIN_URL || ""}user/search/${keyword}`;
      const response = await axios.get<User[]>(url, config);
      return response.data;
    } catch (error) {
      // @ts-ignore
      throw error;
    }
  }
}

export default UserAdminService;
