import React, { useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";
import { Button, Modal, message } from "antd";
import { AxiosError } from "axios";
import { AccountBookOutlined, CheckOutlined, CloseOutlined, DeleteOutlined, EditOutlined } from "@ant-design/icons";
import useColors from "../Hooks/useColors";
import useConfig from "../Hooks/useConfig";
import { Accounts, ApiUser, User, userType } from "../type";
import AccountService from "../services/AccountService";
import ApiUsersService from "../services/ApiUsersService";
import ReactPaginate from "react-paginate";
import UserAdminService from "../services/UserAdminService";
const key = "Loading";
const token = localStorage.getItem("token");
const Users: React.FC = () => {
  const [, Colors] = useColors();
  const navigate = useNavigate();
  const [messageApi, contextHolder] = message.useMessage();
  const [id, setid] = useState<string>("");
  const [keyword, setkeyword] = useState<string>("");
  const [type, settype] = useState<userType>(userType.admin);
  const [password, setpassword] = useState<string>("");
  const [username, setUsername] = useState<string>("");
  const [loading, setLoading] = useState(false);
  const [AccountModal, setAccountModal] = useState(false);
  const [open, setOpen] = useState(false);
  const [users, setUsers] = useState<User[]>([]);
  const [isSave, setIsSave] = useState<boolean>(false);
  const [refresh, setrefresh] = useState<boolean>(false);
  const [page, setpage] = useState<number>(1);
  const [accounts, setaccounts] = useState<Accounts[]>([]);
  const [UsersAccounts, setUsersAccounts] = useState<Accounts[]>();
  const [totalpages, settotalpages] = useState<number>(0);
  const config = useConfig();
  const logout = () => {
    localStorage.removeItem("token");
    localStorage.removeItem("user_id");
    navigate("/Login", { replace: true });
  };
  const reset = () => {
    setUsername("");
  };
  const ErrorMessage = () => {
    messageApi.open({
      key,
      type: "error",
      content: "Error!",
      duration: 2,
    });
  };
  const successMessage = () => {
    messageApi.open({
      key,
      type: "success",
      content: "تم!",
      duration: 2,
    });
  };
  const endCall = () => {
    setrefresh(!refresh);
    setLoading(false);
    reset();
    setOpen(false);
    successMessage();
  };
  const save = async () => {
    try {
      setLoading(true);
      await UserAdminService.create(username, password, type, config);
      endCall();
    } catch (error) {
      // Handle error
      console.log(error);
      const e = error as AxiosError;
      console.log(e.response?.data);
      const err = error as { message: string; status: number };
      if (err.status === 422) {
        messageApi.open({
          key,
          type: "error",
          content: err.message,
          duration: 2,
        });
      } else ErrorMessage();
      setLoading(false);
    }
  };
  const update = async () => {
    try {
      setLoading(true);
      await UserAdminService.update(id, username, password, config);
      endCall();
    } catch (error) {
      // Handle error
      console.log(error);
      const e = error as AxiosError;
      console.log(e.response?.data);
      ErrorMessage();
      setLoading(false);
    }
  };

  const search = async () => {
    try {
      messageApi.open({
        key,
        type: "loading",
        content: "جار التحميل...",
        duration: 0,
      });
      const result = await UserAdminService.search(keyword, config);
      setUsers(result);
      successMessage();
    } catch (error) {
      // Handle error
      ErrorMessage();

      const err = error as AxiosError<{ error: string }>;
      if (err.response?.status === 401) {
        logout();
      }
    }
  };
  const addOrRemoveCode = async (account: Accounts) => {
    try {
      setLoading(true);
      console.log(id);

      if (UsersAccounts?.includes(account)) {
        await UserAdminService.removeAccountFromUser(id, account.id, config);
        setUsersAccounts((ar) => ar?.filter((ac) => ac.id !== account.id));
      } else {
        await UserAdminService.addingAccountToUser(id, account.id, config);

        setUsersAccounts((ar) => (ar ? [...ar, account] : [account]));
      }
      endCall();
    } catch (error) {
      // Handle error
      ErrorMessage();
      setLoading(false);
    }
  };
  const dprofile = async () => {
    try {
      setLoading(true);
      await UserAdminService.delete(id, config);
      endCall();
    } catch (error) {
      // Handle error
      ErrorMessage();
      setLoading(false);
    }
  };
  const closeAccountModal = () => {
    setAccountModal(false);
  };
  const showAccountModal = () => {
    setAccountModal(true);
  };
  const showModal = () => {
    setOpen(true);
  };

  const handleOk = () => {
    isSave ? save() : update();
  };

  const handleCancel = () => {
    setOpen(false);
    reset();
  };
  const renderProfiles = users.map((Element, index) => {
    return (
      <div
        key={index}
        className="d-flex mb-2 p-2"
        style={{
          borderBottomWidth: 1,
          borderTopWidth: 0,
          borderLeftWidth: 0,
          borderRightWidth: 0,
          borderColor: Colors.dark,
          borderStyle: "dashed",
        }}
      >
        <div style={{ flex: 1 }}>
          <div className="d-flex justify-content-between align-items-center">
            <div className="d-flex align-items-end">
              <div className="h4">{Element.username}</div>
              <div className="h6">{Element.accounts?.map((a) => a.code).join(", ")}</div>
            </div>
            <div>
              <div
                style={{ cursor: "pointer" }}
                onClick={() => {
                  showModal();
                  setUsername(Element.username);
                  setIsSave(false);
                  setid(Element.id);
                }}
              >
                <EditOutlined style={{ fontSize: 30, color: Colors.primary }} />
              </div>
              <div
                style={{ cursor: "pointer" }}
                onClick={() => {
                  setUsersAccounts(Element.accounts);
                  console.log(Element);

                  setid(Element.id);
                  showAccountModal();
                }}
              >
                <AccountBookOutlined style={{ fontSize: 30, color: Colors.primary }} />
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  });
  useEffect(() => {
    const fetchData = async () => {
      try {
        messageApi.open({
          key,
          type: "loading",
          content: "جار التحميل...",
          duration: 0,
        });
        const result = await AccountService.get(config);
        setaccounts(result);
        successMessage();
      } catch (error) {
        // Handle error
        ErrorMessage();

        const err = error as AxiosError<{ error: string }>;
        if (err.response?.status === 401) {
          logout();
        }
      }
    };
    fetchData();
  }, []);
  useEffect(() => {
    const fetchData = async () => {
      try {
        messageApi.open({
          key,
          type: "loading",
          content: "جار التحميل...",
          duration: 0,
        });
        const result = await UserAdminService.get(config, page, type);
        settotalpages(result.pages);
        setUsers(result.users);
        successMessage();
      } catch (error) {
        // Handle error
        ErrorMessage();

        const err = error as AxiosError<{ error: string }>;
        if (err.response?.status === 401) {
          logout();
        }
      }
    };
    fetchData();
  }, [refresh, page, type]);
  const getFooter = () => {
    let data = [
      <Button key="back" onClick={handleCancel}>
        إلغاء
      </Button>,
      <Button key="submit" type="primary" loading={loading} onClick={handleOk}>
        حفظ
      </Button>,
    ];
    !isSave &&
      data.push(
        <Button
          key="delete"
          type="default"
          onClick={() => {
            dprofile();
          }}
          danger
          icon={<DeleteOutlined />}
        />
      );
    return data;
  };
  const handlePageClick = (selectedItem: { selected: number }) => {
    setpage(selectedItem.selected + 1);
  };
  return (
    <>
      <div className="container p-3 pt-4">
        {contextHolder}
        <div className="d-flex justify-content-between">
          <div
            className="p-2 px-4 d-flex mb-3 mx-auto"
            style={{
              backgroundColor: Colors.primary,
              borderRadius: 20,
              cursor: "pointer",
            }}
            onClick={() => {
              settype(type === userType.admin ? userType.user : userType.admin);
              setpage(1);
            }}
          >
            <CheckOutlined style={{ fontSize: 24, color: Colors.light }} />
            <div
              className="mx-auto "
              style={{
                color: Colors.light,
                fontSize: 16,
              }}
            >
              {userType[type]}
            </div>
          </div>
          <div
            className="p-2 px-4 d-flex mb-3 mx-auto"
            style={{
              backgroundColor: Colors.primary,
              borderRadius: 20,
              cursor: "pointer",
            }}
            onClick={() => {
              showModal();
              setIsSave(true);
            }}
          >
            <CheckOutlined style={{ fontSize: 24, color: Colors.light }} />
            <div
              className="mx-auto "
              style={{
                color: Colors.light,
                fontSize: 16,
              }}
            >
              جديد
            </div>
          </div>
        </div>
        {renderProfiles}
        <Modal
          open={AccountModal}
          width={1000}
          title="الحسابات"
          onOk={closeAccountModal}
          onCancel={closeAccountModal}
          footer={[
            <Button key="back" onClick={handleCancel}>
              إلغاء
            </Button>,
          ]}
        >
          {accounts.map((account, index) => {
            return (
              <div
                key={index}
                className="d-flex mb-2 p-2"
                style={{
                  borderBottomWidth: 1,
                  borderTopWidth: 0,
                  borderLeftWidth: 0,
                  borderRightWidth: 0,
                  borderColor: Colors.dark,
                  borderStyle: "dashed",
                }}
              >
                <div style={{ flex: 1 }}>
                  <div className="d-flex justify-content-between align-items-center">
                    <div className="d-flex align-items-end">
                      <div className="h4">{account.code}</div>
                    </div>
                    <div>
                      <div
                        style={{ cursor: "pointer" }}
                        onClick={() => {
                          addOrRemoveCode(account);
                        }}
                      >
                        {UsersAccounts?.includes(account) ? (
                          <CloseOutlined style={{ fontSize: 30, color: Colors.danger }} />
                        ) : (
                          <CheckOutlined style={{ fontSize: 30, color: Colors.success }} />
                        )}
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            );
          })}
        </Modal>
        <Modal open={open} width={1000} title="الحسابات" onOk={handleOk} onCancel={handleCancel} footer={getFooter}>
          <div className="form-floating mb-3">
            <input
              type="text"
              className="form-control"
              value={username}
              onChange={(e) => {
                setUsername(e.target.value);
              }}
            />
            <label htmlFor="floatingInput">اسم المستخدم</label>
          </div>
          <div className="form-floating mb-3">
            <input
              type="text"
              className="form-control"
              value={password}
              onChange={(e) => {
                setpassword(e.target.value);
              }}
            />
            <label htmlFor="floatingInput">كلمة السر</label>
          </div>
        </Modal>
        <ReactPaginate
          breakLabel="..."
          nextLabel="next >"
          onPageChange={handlePageClick}
          pageRangeDisplayed={5}
          pageCount={totalpages}
          previousLabel="< previous"
          renderOnZeroPageCount={null}
          className="pagination justify-content-center my3"
          pageClassName="page-item"
          pageLinkClassName="page-link"
          activeClassName="active"
          disabledClassName="disabled"
          nextClassName="page-item"
          previousClassName="page-item"
          previousLinkClassName="page-link"
          nextLinkClassName="page-link"
        />
      </div>
    </>
  );
};

export default Users;
